<template>
  <div class="hello">

    <el-card class="box-card">
      <div style="margin-bottom: 30px;">
      <el-checkbox v-model="checked" @change="handleCheckedCitiesChange">对方称呼我</el-checkbox>
    
    </div>
    <div style="margin-bottom: 30px;">
      <el-radio-group v-model="radio" @change="handleRadioCitiesChange">
    <el-radio :label="0">我是女的</el-radio>
    <el-radio :label="1">我是男的</el-radio>
   
  </el-radio-group>
    </div>
   
    <!-- <el-input v-model="text" type="textarea" placeholder="请输入称谓" autosize :disabled="true"></el-input> -->
    <div style="min-height: 20px; background-color: gainsboro;">{{text}}</div>
    <h5>点击添加关系</h5>
    <div style="margin-top: 10px;">
      
      <el-tag style="margin-left: 5px; margin-top: 5px;"
    v-for="item in items"
    @click="click(item.label)"
    :key="item.label"
    :type="item.type"
    effect="dark">
    {{ item.label }}
  </el-tag>

    </div>
    
    <!-- <button @click="jisuan()">计算</button> -->
    <div style="background-color: gainsboro;">
      <h3>结果：{{value}}</h3>
    </div>
</el-card>

    
    
  </div>
</template>

<script>
var relationship = require("relationship.js");
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      text:"",
      value:"",
      radio:0,
      checked:false,
      items: [
          { type: '', label: '父' },
          { type: '', label: '母' },
          { type: '', label: '夫' },
          { type: '', label: '妻' },
          { type: '', label: '子' },
          { type: '', label: '女' },
          { type: '', label: '兄' },
          { type: '', label: '弟' },
          { type: '', label: '姐' },
          { type: '', label: '妹' },
          { type: 'danger', label: '<' }

        ]
    }
  },
  methods:{
    jisuan(){
     var v = relationship({text:'七舅姥爷',reverse:true,sex:1})
     this.value = v
      console.log(213)
    },
    /**
     * 
     * @param {
     * var options = {
        text:'',		// 目标对象：目标对象的称谓汉字表达，称谓间用‘的’字分隔
        target:'',	    	// 相对对象：相对对象的称谓汉字表达，称谓间用‘的’字分隔，空表示自己
        sex:-1,			// 本人性别：0表示女性,1表示男性
        type:'default',		// 转换类型：'default'计算称谓,'chain'计算关系链,'pair'计算关系合称
        reverse:false,		// 称呼方式：true对方称呼我,false我称呼对方
        mode:'default',		// 模式选择：使用setMode方法定制不同地区模式，在此选择自定义模式
        optimal:false,       	// 最短关系：计算两者之间的最短关系
      };} event 
     */
    click(event){
      if(this.text == ''||this.text ==null){

        this.text = this.fanyi(event)
      }else if(event == '<'){
        this.text = this.text.substring(0,this.text.length-3);
      }else{

        this.text = this.text+'的'+this.fanyi(event)
      }

      var v = relationship({text:this.text,reverse:this.checked,sex:this.radio})
      console.log(">>>>>>>>>>>", v);
      if(this.text != ''){
        if(v == ''||v == null){
        this.value = "你们之间好像没有关系"
      }else{
        this.value = v
      }
      }else{
        this.value = ""
      }
      

      console.log(event)

    },
    handleCheckedCitiesChange(){
      var v = relationship({text:this.text,reverse:this.checked,sex:this.radio})
     this.value = v
    },
    handleRadioCitiesChange(){
      var v = relationship({text:this.text,reverse:this.checked,sex:this.radio})
     this.value = v
    },
    fanyi(value){
      if(value == '父'){
        return '爸爸'
      }else if(value == '母'){
        return '妈妈'
      
      }else if(value == '夫'){
        return "老公"
      }else if(value == '妻'){
        return "老婆"
      }else if(value == '子'){
        return "儿子"
      }else if(value == '女'){
        return "女儿"
      }else if(value == '兄'){
        return "哥哥"
      }else if(value == '弟'){
        return "弟弟"
      }else if(value == '姐'){
        return "姐姐"
      }else if(value == '妹'){
        return "妹妹"
      }

    }
    
  }
}
</script>

